import { Component, OnInit, ViewChild } from "@angular/core";
import {
  ApexChart,
  ApexAxisChartSeries,
  ChartComponent,
  ApexDataLabels,
  ApexPlotOptions,
  ApexYAxis,
  ApexLegend,
  ApexGrid,
  ApexNonAxisChartSeries,
  ApexResponsive,
} from "ng-apexcharts";
import { EmailReport } from "src/app/emailer/email-report/emailreport.model";
import { EmailerService } from "../services/emailer.service";
import { ActivatedRoute } from "@angular/router";
import { JwtService } from "src/app/shared/jwt.service";

type ApexXAxis = {
  type?: "category" | "datetime" | "numeric";
  categories?: any;
  labels?: {
    style?: {
      colors?: string | string[];
      fontSize?: string;
    };
  };
};

export type ChartOptions = {
  series: ApexAxisChartSeries;
  chart: ApexChart;
  dataLabels: ApexDataLabels;
  plotOptions: ApexPlotOptions;
  yaxis: ApexYAxis;
  xaxis: ApexXAxis;
  grid: ApexGrid;
  legend: ApexLegend;
  responsive: ApexResponsive[];
  colors: string[];
  labels: any;
};
export type ChartOptions2 = {
  colors: string[];
  series: ApexNonAxisChartSeries;
  chart: ApexChart;
  responsive: ApexResponsive[];
  labels: any;
};

@Component({
  selector: "app-email-statistics",
  templateUrl: "./email-statistics.component.html",
  styleUrls: ["./email-statistics.component.css"],
})
export class EmailStatisticsComponent implements OnInit {
  campaign_id = "";
  email_report: EmailReport;
  graph_data = [];

  @ViewChild("chart", { static: true }) chart: ChartComponent;
  public chartOptions: Partial<ChartOptions>;
  public chartOptions1: Partial<ChartOptions2>;

  constructor(
    public emailService: EmailerService,
    private route: ActivatedRoute,
    private jwtService: JwtService
  ) {
    route.paramMap.subscribe((param) => {
      this.campaign_id = param.get("id");
    });
  }

  ngOnInit(): void {
    //get full campaign details
    this.emailService.getStatistics(this.campaign_id).subscribe(
      (res: any) => {
        console.log(res);

        this.email_report = res.data[0];
        var data = res.data[1];
        console.log(Object.values(data));
        // let sum = data.reduce((a, b) => a + b, 0);
        // console.log("Sum", sum);

        this.graph_data = Object.values(data);
        this.graph_data.push(0);
        console.log(this.graph_data);

        console.log(res.data[0]);

        this.chartOptions1 = {
          series: this.graph_data,
          chart: {
            type: "donut",
          },
          colors: [
            "#66C28A",
            "#FFABAC",
            "#BEC7FA",
            "#A4EAF3",
            "#3E4153",
            // "#546E7A",
            // "#26a69a",
            // "#D10CE8",
          ],
          labels: ["Delivered", "Failed", "Submitted", "Sent", "Reject&Block"],
          responsive: [
            {
              breakpoint: 5000,
              options: {
                chart: {
                  width: 410,
                },
                legend: {
                  position: "bottom",
                },
              },
            },
          ],
        };

        this.chartOptions = {
          series: [
            {
              name: "distributed",
              data: this.graph_data,
            },
          ],
          chart: {
            height: 400,
            type: "bar",
            // events: {
            //   click: function (chart, w, e) {
            //     // console.log(chart, w, e)
            //   },
            // },
          },
          colors: ["#66C28A", "#FFABAC", "#BEC7FA", "#A4EAF3", "#3E4153"],
          plotOptions: {
            bar: {
              columnWidth: "55%",
              distributed: true,
            },
          },
          dataLabels: {
            enabled: false,
          },
          legend: {
            show: false,
          },
          grid: {
            show: true,
          },
          xaxis: {
            categories: [
              ["Delivered"],
              ["Failed"],
              ["Submitted"],
              ["Sent"],
              ["Reject&Block"],
            ],
            labels: {
              style: {
                colors: ["#00E396", "#FFABAC", "#BEC7FA", "#A4EAF3", "#3E4153"],
                fontSize: "20px",
              },
            },
          },
        };
      },
      (error) => {
        this.jwtService.error(error);
      }
    );
  }
}
