import { NgModule } from "@angular/core";
import { RouterModule, Routes } from "@angular/router";
import { EmailComposeComponent } from "./email-compose/email-compose.component";
import { EmailReportComponent } from "./email-report/email-report.component";
import { VerifyMailComponent } from "./verify-mail/verify-mail.component";
import { EmailLogsComponent } from "./email-logs/email-logs.component";
import { EmailStatisticsComponent } from "./email-statistics/email-statistics.component";

import { AuthGuard } from "src/app/auth.guard";

const routes: Routes = [
  {
    path: "e_compose",
    component: EmailComposeComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "e_report",
    component: EmailReportComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "e_verify",
    component: VerifyMailComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "e_logs/:id",
    component: EmailLogsComponent,
    canActivate: [AuthGuard],
  },

  {
    path: "e_statistics/:id",
    component: EmailStatisticsComponent,
    canActivate: [AuthGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class EmailerRoutingModule {}
