import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { EmailerService } from "../services/emailer.service";
import { Location } from "@angular/common";
import { ActivatedRoute } from "@angular/router";
import { ngxCsv } from "ngx-csv/ngx-csv";
import * as moment from "moment";

export class Mail_logs {
  campaign_id: any;
  dlr_mask: any;
  total_sms: any;
  submit_date: any;
  receivers: any;
  done_date: any;
  description: any;
  error_code: any;
}

@Component({
  selector: "app-email-logs",
  templateUrl: "./email-logs.component.html",
  styleUrls: ["./email-logs.component.css"],
})
export class EmailLogsComponent implements OnInit {
  resend_mail: FormGroup;
  campaign_id: any;
  mail_logs: Mail_logs[];
  searchedKeyword: string;
  //pagination
  p: number = 1;
  count: number = 10;

  constructor(
    public location: Location,
    private emailService: EmailerService,
    private route: ActivatedRoute,
    public fb: FormBuilder
  ) {
    route.paramMap.subscribe((param) => {
      this.campaign_id = param.get("id");
      console.log("id", param.get("id"));
    });
  }

  ngOnInit(): void {
    //get voice logs details
    this.emailService.mailLogs(this.campaign_id).subscribe(
      (res: any) => {
        this.mail_logs = res.data;
        console.log(res);
      },
      (error) => {
        if (error.status == 401) {
          alert(error.statusText);
          // this.jwtService.error(error);
        }
      }
    );
  }

  searchLogs(val: any) {
    console.log(val);

    val["campaign_id"] = this.campaign_id;
    this.emailService.searchMailLogs(val).subscribe(
      (res: any) => {
        console.log(res);
        // if (res.status == 201) {
        //   this.mail_logs = res.data;
        // } else if (res.status == 202) {
        //   alert(res.message);
        // }
      },
      (error) => {
        if (error.status == 401) {
          // this.jwtService.error(error);
        }
      }
    );
  }
  clearFormValue(searchForm) {
    console.log(searchForm.form.value);
    this.ngOnInit();
    searchForm.reset({
      dlr_mask: "",
      recipient: "",
    });
  }

  //export logs
  exportLogs(searchvalue: any) {
    if (this.mail_logs.length < 2000) {
      this.generateCsv(this.mail_logs);
      // this.generateCsv("mahesh");
    } else {
      searchvalue["campaign_id"] = this.campaign_id;
      console.log(searchvalue);
      this.emailService.mailLogsExport(searchvalue).subscribe(
        (res: any) => {
          // console.log(res);
          if (res.status == 201) {
            /////if data is less than our limit than download instantly
            this.generateCsv(res.data);
          } else if (res.status == 202) {
            ///if data greater than limit schedule for csv file by backend
            // Swal.fire({
            //   position: "top-end",
            //   icon: "info",
            //   title: res.message,
            //   showConfirmButton: false,
            //   timer: 2000,
            // });
            alert(res.message);
          }
        },
        (error) => {
          if (error.status == 401) {
            // this.jwtService.error(error);
            alert(error.statusText);
          }
        }
      );
    }
  }

  generateCsv(mail_logs_val) {
    console.log(mail_logs_val);
    let i = 0;
    // let new_list = this.temp_details;
    let csvFileName = "Mail_logs" + moment().format("YYYYMMDDSSMMHH");
    let finalData = mail_logs_val.map(function (obj) {
      return {
        No: (i = i + 1),
        Recipient: obj.email,
        Subject: obj.subject,
        Status:
          obj.dlr_mask == 923
            ? "Submitted"
            : obj.dlr_mask == 2
            ? "Failed"
            : obj.dlr_mask == 1
            ? "Delivered"
            : obj.dlr_mask == 8
            ? "Sent"
            : obj.dlr_mask == 5
            ? "Rejected&Block"
            : "NOT AVAILABLE",
        SubmitDate: obj.submit_date !== null ? obj.submit_date : "Update Soon",
        DoneDate: obj.done_date !== null ? obj.done_date : "Update Soon", //"update soon"
        ErrorCode: obj.error_code !== null ? obj.error_code : "Update Soon", //"update soon", //
        Description: obj.description !== null ? obj.description : "Update Soon", //"update soon", //
      };
    });
    console.log("finalData", finalData);
    var options = {
      fieldSeparator: ",",
      quoteStrings: '"',
      decimalseparator: ".",
      showLabels: false,
      showTitle: false,
      // title: 'CSV DATA',
      useBom: true,
      noDownload: false,
      headers: [
        "S.No",
        "Recipient",
        "Subject",
        "Status",
        "SubmitDate",
        "DoneDate",
        "ErrorCode",
        "Description",
      ],
    };
    new ngxCsv(finalData, csvFileName, options);
  }

  //resend email by status
  resendMail(dlr_mask) {
    console.log(dlr_mask);
    if (dlr_mask === "") {
      alert("Select status for Resend Message");
    } else {
      //resend sms form
      this.resend_mail = this.fb.group({
        campaign_id: [this.campaign_id],
        status: [dlr_mask],
      });

      this.emailService.resendMail(this.resend_mail.value).subscribe(
        (res: any) => {
          console.log(res);
        },
        (error) => {
          if (error.status == 401) {
            // this.jwtService.error(error);
          }
        }
      );
    }
  }
}
