import { Component, OnInit } from "@angular/core";
import {
  FormBuilder,
  FormControl,
  FormGroup,
  Validators,
} from "@angular/forms";
import { EmailerService } from "src/app/emailer/services/emailer.service";

@Component({
  selector: "app-verify-mail",
  templateUrl: "./verify-mail.component.html",
  styleUrls: ["./verify-mail.component.css"],
})
export class VerifyMailComponent implements OnInit {
  verify_email: [];
  err = null;
  error: any;

  constructor(public fb: FormBuilder, private emailService: EmailerService) {}

  verifyEmail = new FormGroup({
    email: new FormControl("", [Validators.required, Validators.email]),
  });

  //form
  get email() {
    return this.verifyEmail.get("email");
  }

  ngOnInit(): void {
    //get list
    this.emailService.getReqMail().subscribe(
      (res) => {
        this.verify_email = res;
        console.log(res);
      },
      (error) => {
        alert(error);
      }
    );
  }

  verifyMail() {
    if (!this.verifyEmail.valid) {
      alert("Enter Valid Email");
    } else {
      this.emailService.reqVerifiedMail(this.verifyEmail.value).subscribe(
        (res) => {
          if (200) {
            console.log(res);
            alert(res.message);
            this.ngOnInit();
          }
        },
        (error) => {
          if (403) {
            alert("not valid");
          }
          if (401) {
            alert(error.statusText);
          }
          console.log(error);
        }
      );
    }
  }

  deleteVerMail(details) {
    this.emailService.deleteMail(details.verify_id).subscribe(
      (res) => {
        console.log(res);
      },
      (error) => {
        console.log(error);
      }
    );
  }
}
