import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { GlobalConstants } from "src/app/global_constant";

@Injectable({
  providedIn: "root",
})
export class EmailerService {
  url = GlobalConstants.url;

  constructor(private http: HttpClient) {}

  ////////////////----------------------send email/compose start---------------------///////////
  public getListVerified() {
    return this.http.get<any>(this.url + "getVerifiedMail");
  }
  public sendEmail(data) {
    return this.http.post<any>(this.url + "send_mail", data);
  }
  ////////////////----------------------send email/compose  end---------------------///////////

  ////////////////----------------------report start---------------------///////////
  //get voice report
  public getMailReport() {
    return this.http.get<any>(this.url + "mail_report");
  }

  //filtter or search report
  public searchMailFilter(data) {
    return this.http.post<any>(this.url + "mailfilterReport", data);
  }

  public mailReportExport(data) {
    return this.http.post<any>(this.url + "mailreportExport", data);
  }
  ////////////////----------------------report end---------------------///////////

  ////////////////----------------------logs start---------------------///////////
  public mailLogs(id: any) {
    return this.http.get<any>(this.url + "mail_logs/" + id);
  }
  public resendMail(data) {
    return this.http.post<any>(this.url + "resendMail", data);
  }
  public searchMailLogs(data: any) {
    return this.http.post<any>(this.url + "maillogsFilter", data);
  }

  public mailLogsExport(data) {
    return this.http.post<any>(this.url + "voiceLogsExport", data);
  }
  ////////////////----------------------logs end---------------------///////////

  ////////////////----------------------view start---------------------///////////
  public getStatistics(id: any) {
    return this.http.get<any>(this.url + "mailStatistics/" + id);
  }
  ////////////////----------------------view end---------------------///////////

  ////////////////----------------------verify email start---------------------///////////
  public getReqMail() {
    return this.http.get<any>(this.url + "verifyMailStatus");
  }

  public reqVerifiedMail(data) {
    return this.http.post<any>(this.url + "verifyMail", data);
  }

  public deleteMail(id: any) {
    return this.http.get<any>(this.url + "deleteMail/" + id);
  }

  ////////////////----------------------verify email end---------------------///////////
}
