import { Component, OnInit, ViewChild } from "@angular/core";
import { Location } from "@angular/common";
import * as moment from "moment";
import { DaterangepickerDirective } from "ngx-daterangepicker-material";
import { ngxCsv } from "ngx-csv/ngx-csv";
import { EmailReport } from "./emailreport.model";
import { EmailerService } from "../services/emailer.service";
import { JwtService } from "src/app/shared/jwt.service";
import { NgbModal, ModalDismissReasons } from "@ng-bootstrap/ng-bootstrap";

@Component({
  selector: "app-email-report",
  templateUrl: "./email-report.component.html",
  styleUrls: ["./email-report.component.css"],
})
export class EmailReportComponent implements OnInit {
  email_report: EmailReport[];
  verifyMail: [];
  selected: any = [
    moment().format("DD-MM-YYYY"),
    moment().format("DD-MM-YYYY"),
  ];
  maxDate: any;
  alwaysShowCalendars: boolean;
  showReportOnMobile: boolean;
  showReportNormal: boolean;
  emailContent: string;
  isSubmitBtnDisabled: boolean;
  //pagination
  p: number = 1;
  count: number = 10;
  @ViewChild(DaterangepickerDirective, { static: false })
  pickerDirective: DaterangepickerDirective;

  constructor(
    public location: Location,
    public emailService: EmailerService,
    private jwtService: JwtService,
    private modalService: NgbModal
  ) {
    this.isSubmitBtnDisabled = false;
  }

  ngOnInit(): void {
    this.alwaysShowCalendars = true;
    this.showReportOnMobile = false;
    this.showReportNormal = true;

    this.emailService.getMailReport().subscribe(
      (res: any) => {
        this.email_report = res;
        console.log("Res", res);
      },
      (error) => {
        // if (error.status == 401) {
        //   alert(error.statusText);
        // }
        // console.log(error);

        this.jwtService.error(error);
      }
    );

    //get list of verified mail
    this.emailService.getListVerified().subscribe(
      (res) => {
        console.log(res);
        this.verifyMail = res;
      },
      (error) => {
        if (403) {
          alert(error.statusText);
        }
        if (error.status == 401) {
          alert(error.statusText);
          this.jwtService.error(error);
        }
      }
    );
  }

  change(val: any) {
    this.maxDate = moment(val);
    // var start = ;
    // var end = ;
    // console.log(start);
    // console.log(end);
    this.selected = [
      moment(val.startDate._d).format("DD-MM-YYYY"),
      moment(val.endDate._d).format("DD-MM-YYYY"),
    ];
  }

  searchReport(val: any) {
    console.log(val);

    this.emailService.searchMailFilter(val).subscribe(
      (res: any) => {
        console.log(res);
        if (res.status == 201) {
          if (res.data[0].email) {
            console.log("data with email/receipents");
            this.email_report = res.data;
            this.showReportOnMobile = true;
            this.showReportNormal = false;
          } else if (res.data.email == null) {
            console.log("data without email normal");
            this.email_report = res.data;
            this.showReportOnMobile = false;
            this.showReportNormal = true;
          }
        } else if (res.status == 202) {
          alert(res.status);
          console.log("no data found");
        }
      },
      (error) => {
        if (error.status == 401) {
          this.jwtService.error(error);
        }
        if (403) {
          alert(error.statusText);
        }
      }
    );
  }
  clearFormValue(searchForm) {
    console.log(searchForm);
    searchForm.reset({
      sender: "",
      type: "",
      size: "",
      date: [moment().format("DD-MM-YYYY"), moment().format("DD-MM-YYYY")],
      number: "",
      campaign_id: "",
      campaign_name: "",
    });
    this.ngOnInit();
  }

  exportReport(val: any) {
    console.log(val);
    this.emailService.mailReportExport(val).subscribe(
      (res: any) => {
        this.isSubmitBtnDisabled = true;
        console.log(res);
        if (res.status == 201) {
          if (res.data.length > 0) {
            let i = 0;
            let csvFileName =
              "Email_Report" + moment().format("YYYYMMDDSSMMHH");
            let finalData = res.data.map(function (obj) {
              return {
                No: (i = i + 1),
                Subject: obj.subject,
                CampaignUid: obj.campaign_id,
                SenderName: obj.sender_source,
                SenderEmail: obj.sender_email,
                Recipents: obj.email,
                TotalCredit: obj.total_messages,
                Status:
                  obj.dlr_mask == 923
                    ? "Submitted"
                    : obj.dlr_mask == 2
                    ? "Failed"
                    : obj.dlr_mask == 1
                    ? "Delivered"
                    : obj.dlr_mask == 8
                    ? "Sent"
                    : obj.dlr_mask == 5
                    ? "Rejected&Block"
                    : "None",
                SubmitDate:
                  obj.submit_date !== null ? obj.submit_date : "Update Soon",
                DoneDate:
                  obj.done_date !== null ? obj.done_date : "Update Soon",
              };
            });
            console.log(finalData);
            var options = {
              fieldSeparator: ",",
              quoteStrings: '"',
              decimalseparator: ".",
              showLabels: false,
              showTitle: false,
              // title: 'CSV DATA',
              useBom: true,
              noDownload: false,
              headers: [
                "S.No",
                "Subject",
                "CampaignUid",
                "SenderName",
                "SenderEmail",
                "Recipents",
                "TotalCredit",
                "Status",
                "SubmitDate",
                "DoneDate",
              ],
            };
            new ngxCsv(finalData, csvFileName, options);
            this.isSubmitBtnDisabled = false;
          } else {
            alert(res.message);
          }
        } else if (res.status == 202) {
          alert(res.message);
        }
      },
      (error) => {
        this.jwtService.error(error);
      }
    );
  }

  ////---------model part dynamic column---------
  open(content, report) {
    console.log(report);
    this.emailContent = report.email_content;

    this.modalService.open(content, { size: "lg" });
  }
}
